@import url(https://prism-ui.netlify.app/component.css);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@400;600;700&display=swap);
@import url(https://kit-pro.fontawesome.com/releases/v5.15.3/css/pro.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
* {
  padding: 0;
  margin: 0;
  font-family: "Titillium Web", sans-serif;
}
.body {
  background-color: #f6f8f8;
}
.App {
  font-family: sans-serif;
  text-align: center;
}

.nav-logo {
  width: 3rem;
  margin: 0.2rem;
  margin-left: 2rem;
}

.product-showcase {
  display: -webkit-flex;
  display: flex;
  /* margin: 0.6rem; */
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}
.nav-box {
  width: 100%;
  position: fixed;
  z-index: 999;
  top: 0;
}

.nav-btn {
  margin: 0px;
}
.cart-last {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.active-link {
  color: #f3756d;
}

.value > span {
  position: relative;
  color: white;
  padding: 0px 3px;
  background-color: red;
  border-radius: 50%;
  width: 9px;
  height: 9px;
  bottom: 0.7rem;
  right: 0.3rem;
  font-size: small;
}
@media screen and (min-width: 490px) {
  .mob-nav-box {
    display: none;
  }
}

@media screen and (max-width: 490px) {
  .disable {
    display: none;
  }
  .mob-nav-box {
    position: fixed;
    bottom: 0;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    list-style: none;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    background-color: white;
    z-index: 1000;
    height: 4rem;
    -webkit-align-items: center;
            align-items: center;
    box-shadow: rgb(0 0 0 / 25%) 0px 0.0625em 0.0625em,
      rgb(0 0 0 / 25%) 0px 0.125em 0.5em,
      rgb(255 255 255 / 10%) 0px 0px 0px 1px inset;
  }
  .mob-nav-btn {
    font-size: 1.2rem;
    color: var(--primary-color);
  }
}

.content-page {
  margin-top: 4.5rem;
  margin-bottom: 4.5rem;
}

.card-v {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em,
    rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em,
    rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
  width: 15.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.card-v:hover {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

.content-card {
  /* padding: 0rem 1rem 1rem 1rem; */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.content-card > .div1,
.div3 {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.content-card > .div2 {
  font-size: small;
  margin: 0.5rem 0rem;
  text-align: left;
}

.price {
  font-weight: bold;
}

.quantity-btn > button {
  background-color: transparent;
  outline: none;
  border: none;
  padding: 0.2rem 0.7rem;
  margin: 0.4rem;
  font-size: 1.1rem;
  font-weight: 900;
  border-radius: 4px;
}

.quantity-btn > button:hover {
  background-color: #bcbcbc;
}

.cart-btn {
  padding: 0.4rem 0.3rem;
  color: white;
  outline: none;
  border: none;
  border-radius: 5px;
}

.btn-red {
  background-color: #ff1e50;
  margin-left: 0.2rem;
}

.btn-blue {
  background-color: #2b4296;
  margin-left: 0.2rem;
}

.btn-red-disabled {
  background-color: #df5573;
}



